const app = getApp();
export default {
    data() {
        return {
            list: [],
            nomore: 0,
            page: 1
        };
    },
    // 0申请 1审核中 2通过 3拒绝
    onLoad() {
        this.loadData("refresh");
    },
    onShareAppMessage() {},
    async onReachBottom() {
        await this.loadData();
    },
    async onPullDownRefresh() {
        await this.loadData("refresh");
        uni.stopPullDownRefresh();
    },
    methods: {
        previewImg(url){
            uni.previewImage({
                current: url,
                urls: [url]
            });
        },
        delItem(item, index) {
            uni.showModal({
                title: "提示",
                content: "确定删除吗?",
                success: async res => {
                    if (res.confirm) {
                        const res = await app.request(
                            this.$Url + `/clmsj/live/deleteLiveGoods`,
                            {
                                id: item.id
                            }
                        );

                        if (res.data.code == 0) {
                            this.$api.msg("删除成功");
                            this.list.splice(index, 1);
                        }
                    }
                }
            });
        },
        refuseReason(item) {
            uni.showModal({
                title: "拒绝理由",
                content: item.refuse,
                showCancel: false
            });
        },
        async loadData(type) {
            return
            if (type == "refresh") {
                this.list = [];
                this.page = 1;
                this.nomore = 0;
            }

            if (this.nomore == 1) {
                return;
            }
            const res = await app.request(
                this.$Url + "/clmsj/live/checkLiveGoodsList",
                {
                    page: this.page
                }
            );

            if (res.data.data.length == 0) {
                this.nomore = 1;
                return;
            }

            if (res.data.code == 0) {
                this.page++;
                this.list = this.list.concat(res.data.data);
            }
        }
    }
};
